@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
body {
  background-color: #FCF6F6;
  background-color: var(--lightPinkColor);
}
:root {
  --lightPinkColor : #FCF6F6;
  --secondaryPinkColor : #F2D8D8;
  --darkPinkColor : #EA7272;
  --darkscrollPinkColor : #d34c4c;
  --lightGreyColor : #D5CFCF;
  --secondaryGreyColor :#A3A3A3; 
  --darkGreyColor : #515050;
}

::-webkit-scrollbar {
  width: .5rem;
  background-color: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background-color: #EA7272;
  background-color: var(--darkPinkColor);
}
::-webkit-scrollbar-thumb:hover {
  background-color: #d34c4c;
  background-color: var(--darkscrollPinkColor);
}

header {
    height: 5rem;
    background-color: var(--secondaryPinkColor);
    padding: 1rem;
    box-shadow: 0 0 15px 0 rgba(0,0,0,0.3);
}
header > h1 {
    color: var(--darkPinkColor);
    font-weight: 600;
    margin-top: 5px;
    font-size: 24px;
    background-color: var(--secondaryPinkColor);
    margin-left: 20px;
}

footer {
    /* position: fixed; */
    /* background-color: var(--secondaryPinkColor); */
    /* bottom: 0; */
    text-align: center;
    color: var(--darkPinkColor);
    width: 100%;
    padding: 10px;
}

.note {
    color: var(--darkGreyColor);
    border-radius: 10px;
    background-color: #fff;
    padding: 10px;
    width: 300px;
    margin: 30px;
    box-shadow: 0 2px 5px #ccc;
    float: left;
}
.note h1 {
    font-weight: 500;
    font-size: 1.5em;
}
.note p {
   white-space: pre-wrap;
   word-wrap:break-word;
   margin-bottom: 10px;
}
.note > button {
    float: right;
    border: none;
    outline: none;
    background-color: white;
    color: var(--darkPinkColor);
    cursor: pointer;
}
.create {
    width: 35%;
    margin: auto;
    background-color: #fff;
    border-radius: 10px;
}
form {   
    margin:20px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: relative;
}
form > input , form > textarea {
    font-size: 1.1em;
    margin: 10px;
    outline: none;
    padding: 10px;
    border: none;
    color: var(--darkGreyColor);
}
form > input::-webkit-input-placeholder, form > textarea::-webkit-input-placeholder {
    color: var(--lightGreyColor);
}
form > input:-ms-input-placeholder, form > textarea:-ms-input-placeholder {
    color: var(--lightGreyColor);
}
form > input::placeholder, form > textarea::placeholder {
    color: var(--lightGreyColor);
}
form > input:focus , form > textarea:focus {
    background-color: var(--lightPinkColor);
}
.addBtn {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    color: #fff;
    font-size: 2rem;
    text-align: center;
    font-weight: 500;
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    right: 2rem;
    bottom: -.7rem;
    background-color: var(--darkPinkColor);
    border: none;
    outline: none;
    box-shadow: 1px 1px 12px 1px rgba(0,0,0,0.2);
}
